body{
  background:#3D9970;
}
*{
  margin:0px;
  padding:0px;
}
.container {

  width:360px;
  height:50vh;
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  
  border:2px solid black;
  background:#AAAAAA;
  display: grid;
  grid-template-areas:
   "result result result result"
    "display display display display" 
    "button1 button1 button2 button3"
    "button4 button5 button6 button7"
    "button8 button9 button10 button11"
    "button12 button13 button14 button17"
    "button15 button15 button16 button17";
  gap: 1px; /* Add 10px gap between rows and columns */
}
.container button {
  padding: 10px;
  border: none;
  background-color: #28231D;
  color: white;
  font-size: 16px;
  text-align: center;
}
#result{
  grid-area:result;
  text-align: right;
}
#display{
  grid-area:display;
  text-align:right;
}
#clear {
  grid-area: button1;
}

#divide{
  grid-area: button2;
}

#multiply{
  grid-area: button3;
}

#seven{
  grid-area: button4;
}

#eight{
  grid-area: button5;
}

#nine{
  grid-area: button6;
}

#subtract{
  grid-area: button7;
}

#four{
  grid-area: button8;
}

#five{
  grid-area: button9;
}
#six{
  grid-area: button10;
}

#add{
  grid-area: button11;
}

#one{
  grid-area: button12;
}

#two{
  grid-area: button13;
}

#three{
  grid-area: button14;
}

#zero{
  grid-area: button15;
}

#decimal{
  grid-area: button16;
}

#equals{
  grid-area: button17;
}
#display,#result{
  font-size:20px;
}
